import axios from "axios";
import Vue from "vue";
import whiteList from "./whiteList.js";


// 创建请求实例
const req = axios.create({
	baseURL: process.env.NODE_ENV === "development" ? "/ff" : process.env.HOST,
	timeout: 20000,
	headers: {"X-Requested-With": "XMLHttpRequest"}
});

let isRefreshing = false;

let subscribers = [];

const addSubscribers  = (cb) => subscribers.push(cb);

const doSubscribers = (token) => subscribers.map((cb) => cb(token));

const isExpired = () =>  (localStorage.getItem("token_expiresIn") - new Date.now()) >= 30000;

const refreshToken = () => axios({
	headers: {"Authorization": localStorage.getItem(refresh_token)},
	url: window.vm$api.login_refresh_token
})
req.interceptors.request.use((request) => {
	const _this = window.vm;
	/** 白名单中的请求不进行拦截 */
	if (whiteList.includes("/" + _this.$utils.getAbsolutePath(request.url))) {
		return request;
	}
	if (localStorage.getItem("token")) {
		request.headers.Authorization = localStorage.getItem("token");
		if (request.reqType === "formData") {
			request.headers["Content-Type"] = "multipart/form-data";
		}
	}
	console.log(request);
	console.log(isExpired());
	if (isExpired()) {
		if (!isRefreshing) {
			isRefreshing = true;
			axios({
				method: "post",
				url: _this.$api.login_refresh_token,
				headers: { "Authorization": localStorage.getItem("refresh_token") }
			}).then((res) => {
				isRefreshing = true;
				if (res.data.code === "200") {
					localStorage.setItem("token", res.data.result.token);
		        	localStorage.setItem("refresh_token", res.data.result.refresh_token);
		        	localStorage.setItem("expires_in", res.data.result.token_expiresIn);
					_this.$message({ message: res.data.msg, type: "success", duration: 3000 });
					doSubscribers(res.data.result.token);
				} else {
					_this.$message({ message: res.data.msg, type: "warning", duration: 2000, onClose: () => _this.$router.push({ path: "/login" })});
				}
			}).catch((err) => {
				_this.$message({ message: err.response.data.msg, type: "warning", duration: 2000, onClose: () => _this.$router.push({ path: "/login" })});
			});
		}
		/** expired后所有的请求都加入到队列中 */
		let retry = new Promise((resolve, reject) => {
			addSubscriber((token) => {
				req.headers.Authorization = token;
				resolve(request);
			});
		});
		return retry;
	} else {
		return request;
	}
}, (err) => {
	return Promise.reject(err);
});

req.interceptors.response.use((res) => {
	return res.data;
}, (err) => {
	const _this = window.vm;
	if (!err.response) {
		return Promise.resolve(err);
	}
	switch (err.response.status) {
		case 401:
		case 403:
			_this.$message({message: err.response.data.msg, type: "error", duration: 3000});
			_this.$router.push({ path: "/login" });
			break;
		default: ;
	}
});

Vue.prototype.$http = req;

export default req;



